import loading from "@/components/loading";
export default {
  components: {
    loading
  },
  data() {
    return {
      loadingShow: true
    };
  },
  onLoad() {
    this.whetherTheLogin(); // 是否登录
  },
  methods: {
    whetherTheLogin() {
      // 转跳
      setTimeout(() => {
        this.loadingShow = false;
        if (uni.getStorageSync("UID") && uni.getStorageSync("USERINFO")) {
          // 已经登录直接跳转首页
          uni.reLaunch({ url: "/pages/plaza" });
        } else {
          uni.reLaunch({ url: "/pages/login" });
        }
      }, 400);
    }
  }
};
